
import React, { useState } from 'react';

// =============================================
// AI CHATBOT COMPONENT
// =============================================
export default function AIChatbot() {
  const [isOpen, setIsOpen] = useState(false);
  const [messages, setMessages] = useState([
    { type: 'bot', text: '!   AI  MAISON\n\n:\n \n \n \n ' }
  ]);
  const [input, setInput] = useState('');
  const [isTyping, setIsTyping] = useState(false);
  const messagesEndRef = React.useRef(null);
  
  // FAQ responses
  const faqResponses = {
    '': { 
      keywords: ['', '', 'delivery', 'shipping'],
      response: ' :\n\n  1,500\n  2-3 \n  SMS/Email\n  Kerry, Flash, '
    },
    '': {
      keywords: ['', 'size', '', ''],
      response: ' :\n\n S:  34-36"\n M:  36-38"\n L:  38-40"\n XL:  40-42"\n\n  1 '
    },
    '': {
      keywords: ['', '', 'return', 'exchange'],
      response: ' :\n\n  14 \n  \n  support@maison.com\n  5-7 '
    },
    '': {
      keywords: ['', 'recommend', '', 'best'],
      response: ' :\n\n1. Cropped Relaxed Button-Down - 1,990\n2. Lanvin Embroidered - 17,147\n3. JADED LONDON JEANS - 3,490\n\n "Shop Now" !'
    },
    '': {
      keywords: ['', 'contact', '', 'email'],
      response: ' :\n\n Email: support@maison.com\n Line: @maison\n Tel: 02-xxx-xxxx\n\n 9:00 - 18:00 . '
    },
    '': {
      keywords: ['', '', 'payment', ''],
      response: ' :\n\n /\n \n PromptPay\n  (COD)\n\n 100%'
    }
  };
  
  const scrollToBottom = () => {
    setTimeout(() => {
      messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
    }, 100);
  };
  
  const handleSend = async (e) => {
    e.preventDefault();
    if (!input.trim()) return;
    
    const userMessage = input.trim();
    setMessages(prev => [...prev, { type: 'user', text: userMessage }]);
    setInput('');
    setIsTyping(true);
    scrollToBottom();
    
    // Simulate AI thinking delay
    await new Promise(resolve => setTimeout(resolve, 1000 + Math.random() * 1000));
    
    // Find matching FAQ response
    let response = '  \n\n:\n \n \n \n \n ';
    
    const lowerInput = userMessage.toLowerCase();
    for (const key in faqResponses) {
      if (faqResponses[key].keywords.some(k => lowerInput.includes(k))) {
        response = faqResponses[key].response;
        break;
      }
    }
    
    setMessages(prev => [...prev, { type: 'bot', text: response }]);
    setIsTyping(false);
    scrollToBottom();
  };
  
  return (
    <>
      <button 
        className={`ai-chatbot-btn ${isOpen ? 'active' : ''}`} 
        onClick={() => setIsOpen(!isOpen)}
        title="AI Assistant"
      >
        
      </button>
      
      {isOpen && (
        <div className="ai-chatbot-panel">
          <div className="ai-chatbot-header">
            <span className="ai-header-title"> MAISON AI Assistant</span>
            <button className="ai-close-btn" onClick={() => setIsOpen(false)}></button>
          </div>
          
          <div className="ai-chatbot-messages">
            {messages.map((msg, idx) => (
              <div key={idx} className={`ai-message ${msg.type}`}>
                {msg.text}
              </div>
            ))}
            {isTyping && (
              <div className="ai-message bot typing">
                <span className="typing-dots">
                  <span>.</span><span>.</span><span>.</span>
                </span>
              </div>
            )}
            <div ref={messagesEndRef} />
          </div>
          
          <form className="ai-chatbot-input" onSubmit={handleSend}>
            <input
              type="text"
              value={input}
              onChange={(e) => setInput(e.target.value)}
              placeholder="..."
            />
            <button type="submit"></button>
          </form>
        </div>
      )}
    </>
  );
}
